@import url(https://fonts.googleapis.com/css2?family=Langar&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Langar&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Langar&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.nav-base{
    width: 100%;
    background: black;
    min-height: 10vh;
}

.nav{
    width: 70%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav-logo{
    color: #fff;
    font-family: 'Langar', cursive;
    font-size: 2rem;
}

.nav-items{
    display: flex;
    list-style: none;
    color: #fff;
    width: 20%;
    justify-content: space-between;
}



.nav-item:hover{
    cursor: pointer;
    color: rgb(167, 165, 165);
}



img{
    width:100%;
    height:90vh;
}
.btn{
    width: 100%;
    position: absolute;
    bottom: 90px;
    display: flex;
    justify-content: center;
}

button{
padding: 20px 50px;
border: 2px solid white;
border-radius: 10px;
background: transparent;
color: white;
font-size: 2rem;
font-family: 'Langar', cursive;
}

button:hover{
    /* background: radial-gradient(#f7e893,#ffd700); */
    background: radial-gradient(#29323c,black);
    color: white;
  
}
.body{
    width: 100%;
    text-align: center;
    margin-top: 10vh;
    
}

.daily-items{
    width: 80%;
    margin: auto;
    display: grid;
    grid-template-columns:  1fr 1fr 1fr ;
    text-align: center;
    grid-column-gap: 20px;
    grid-row-gap: 10px;
}

.daily-header{
    margin-bottom: 10vh;
    font-family: 'Poppins', sans-serif;
    font-size: 50px;
    background: linear-gradient(to right,#485563,#29323c);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.container{
    margin-top: 20px;
    background: radial-gradient(#485563,#29323c);
    border-radius: 10px;
    height: 500px;
    padding-top: 10px;
}

.prop-img{
    margin-top: 10px;
    width: 55%;
    height: 55%;
}

.prop-title{
    font-family: 'Langar', cursive;
    color: #fff;
    margin-bottom: 0;
}

.prop-desc{
    margin-top: 0;
    font-family: 'Poppins', sans-serif;
    color: rgb(222, 222, 222);
}

.prop-btn{
    padding: 8px 22px;
    border: 1px solid #fff;
    color: white;
    margin-top: 10px;
    font-size: 20px;
}

.prop-btn:hover{
    color: #29323c;
    background: white;
}



